<template lang='pug'>
  div
    i#burguer-menu.icon-menu.burger-button(@click="hideShow()")
    header.header
      nav.nav
          ul.nav--items
              li.nav--item
                  a(href="#projects") Proyectos
              li.nav--item 
                  a(href="#trainings") Formación
              li.nav--item 
                  a(href="#experience") Experiencia
              li.nav--item 
                  a(href="#contact") Contácto
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      nav: ["Proyectos", "Formación", "Experiencia", "Contacto"]
    };
  },
  methods: {
    hideShow() {
      const menu = document.querySelector(".nav");
      if (menu.classList.contains("is-active")) {
        menu.classList.remove("is-active");
      } else {
        menu.classList.add("is-active");
      }
    }
  }
};
</script>


