<template lang='pug'>
  section#projects.projects
    .projects--title
        h2 Proyectos
    .projects--container 
      .project
        .project--img
          img(src="../assets/josfifteen.png")
        .project--content
          .project--title
            h3 Josfifteen
            a.btn__view(@click='showDetails1()') {{ btnView1 }}
          .project--description(v-if="display1")
            p  Tienda virtual (e-commece) real desarrollado en wordpress. 
          .project--tools
            .tool.tool--wp wordpress
            .tool.tool--woo woocommerce
            .tool.tool--square square
          .project--btn(v-if="display1")
            a.btn__project(target="_blank" href="https://josfifteen.com/") Ver Proyecto
      .project
        .project--img
          img(src="../assets/yourfreesecurity.png")
        .project--content
          .project--title
            h3 JV Solutions LLC
            a.btn__view(@click='showDetails2()') {{ btnView2 }}
          .project--description(v-if="display2")
            p Sitio web de servicio de cámaras de seguridad.
          .project--tools
            .tool.tool--wp wordpress
            .tool.tool--css css
            .tool.tool--html html
          .project--btn(v-if="display2")
            a.btn__project(target="_blank" href="https://yourfreesecuritysystem.com/") Ver Proyecto
      .project
        .project--img
          img(src="../assets/musicalacademy.png")
        .project--content
          .project--title
            h3 Musical Academy
            a.btn__view(@click='showDetails3()') {{ btnView3 }}
          .project--description(v-if="display3")
            p Proyecto de ejemplo de una tienda de instrumentos musicales.
          .project--tools
            .tool.tool--wp responsive design
            .tool.tool--js javascript
            .tool.tool--html html
          .project--btn(v-if="display3")
            a.btn__project(target="_blank" href="https://rhonsanchez.github.io/MusicalAcademy") Ver Proyecto
    .repository
      h3 ¿Quieres saber más de mis proyectos?
      p Puedes entrar a mi repositorio de GitHub en: 
        a(href='github.com/RhonSanchez ') github.com/RhonSanchez 
        | y conocer más sobre lo que he hecho en toda mi trayectoria como desarrollador.
</template>

<script>
export default {
  name: "Project",
  data: () => ({
    display1: false,
    display2: false,
    display3: false,
    btnView1: "Ver Más",
    btnView2: "Ver Más",
    btnView3: "Ver Más"
  }),
  methods: {
    showDetails1() {
      this.display1 = !this.display1;
      if (this.display1) this.btnView1 = "Ver Menos";
      else this.btnView1 = "Ver Más";
    },

    showDetails2() {
      this.display2 = !this.display2;
      if (this.display2) this.btnView2 = "Ver Menos";
      else this.btnView2 = "Ver Más";
    },

    showDetails3() {
      this.display3 = !this.display3;
      if (this.display3) this.btnView3 = "Ver Menos";
      else this.btnView3 = "Ver Más";
    }
  }
};
</script>


